// unclassed html elements only

*:not(span[id^="toc-"])+h2 { margin-top: var(--space-l); }
*+h3 { margin-top: var(--space-l); }
*+h4 { margin-top: var(--space-s); }
*+h5,*+h6 { margin-top: var(--space-xs); }

h1{
  margin-bottom: var(--space-m);
}
.view-header h1{
  margin-bottom: var(--space-xl);
}
h2{
  margin-bottom: var(--space-s);
}
h3,h4,h5,h6{
  margin-bottom: var(--space-xs);
}
.layout__region--second > .block:first-child > .field:first-child
{
  & > h2,& > h3,& > h4,& > h5,& > h6{
    &:first-child{
      margin-top: 0;
    }
  }
}

h2{
  & + div.contextual + div.field{
    margin-top: var(--space-l);
  }
}

h3{
  letter-spacing: .5px;
}

h2,h3,h4,h5,h6{
  &+h3,&+h4,&+h5,&+h6{
    margin-top: var(--space-m);
  }
}

h2+.laurel-side-by-side>div{
  &>h3:first-child{
    margin-top: 0;
  }
  &:last-child>*:last-child{
    margin-bottom: 0;
  }
}
h2.field__label{
  font-weight: normal;
}
.block-entity-viewnode>span[id^="toc-"]+h2{
  margin-top: 0;
}
.block+.block h2{
  margin-top: var(--space-xl);
}